.md-modal{
	position:fixed;
	left:50%;
	top:50%;
	width:535px;
	height:auto;
	z-index:201;
	-webkit-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
	transform:translate(-50%,-50%);
	.md-modal-transition{
		&.md-show{
			visibility: visible;
			.md-modal-inner{
				opacity:1;
				-webkit-transform: translateY(0px);
			    -ms-transform: translateY(0px);
			    -moz-transform:translateY(0px);
			    transform: translateY(0px);
				transition:all 0.5 ease-out;
				-webkit-transition:all .5 ease-out;
			}
		}
		.md-modal-inner{
			opacity:0;
			// .transition(all,0.3s,ease-out);
			// .transform(20%);
			background:#fff;
		}
	}
	.md-modal-inner{
		padding:60px 50px;
	}
	.alert-tips{
		font-size: 14px;
	    font-weight: 200;
	    text-align: center;
	}
	.confirm-tips{
		min-height:5.65em;
		&:extend(.alert-tips);
	}
	.btn-wrap {
	    margin-top: 20px;
	    text-align: center;
	    font-size: 0;
	}
	.btn-wrap {
		.btn {
		    width: 45%;
		    min-width: 80px;
		    margin: 0 2.5%;
		}
	}
	.md-close {
	    position: absolute;
	    top: 7px;
	    right: 7px;
	    width: 34px;
	    height: 34px;
	    -webkit-transform: scale(0.5);
	    -ms-transform: scale(0.5);
	    transform: scale(0.5);
	    text-indent: -8000px;
	    &:hover{
	    	&:after{
				transition:all 0.3 ease-out;
				-webkit-transition:all .3 ease-out;	
	    	}
	    	&:before{
				    -webkit-transform: rotate(-135deg);
				    -ms-transform: rotate(-135deg);
				    transform: rotate(-135deg);
				    &:extend(&:after);
	    	}
	    	
	    }
	    &:before{
	    	-webkit-transform: rotate(-45deg);
		    -ms-transform: rotate(-45deg);
		    transform: rotate(-45deg);
		    position: absolute;
		    top: 16px;
		    left: -4px;
		    content: "";
		    width: 44px;
		    height: 3px;
		    background: #605f5f;
		    transition:-webkit-transform 0.5 ease-out;
			-webkit-transition:-webkit-transform .5 ease-out;
			transition:transform .5 ease-out;
			transition:transform .5 ease-out,-webkit-transform 0.5 ease-out;
	    }
	    &:after{
	    	&:extend(&:before);
	    }
	}
}
.md-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    z-index: 200;
}

.btn {
    display: inline-block;
    height: 54px;
    line-height: 54px;
    padding: 0 1.2em;
    text-align: center;
    font-size: 14px;
    font-family: "moderat",sans-serif;
    -webkit-transition: all .3s ease-out;
    transition: all .3s ease-out;
    border: 1px solid #d1434a;
    color: #d1434a;
    text-transform: uppercase;
    letter-spacing: .25em;
    white-space: nowrap;
}
.btn--m {
    height: 45px;
    line-height: 45px;
}
.btn--s{

    height:30px;
    line-height:30px;
}
input, select, button {
    vertical-align: middle;
}

.btn--m {
    height: 45px;
    line-height: 45px;
}
.btn--red {
    background-color: #d1434a;
    border-color: #d1434a;
    color: #fff;
}
.btn:hover, .btn[href]:hover {
    background-color: #d1434a;
    color: #fff;
    -webkit-transition: all .3s ease-out;
    transition: all .3s ease-out;
}
.btn--red:hover {
    background-color: #fff;
    color: #d1434a;
}
button {
    background: 0;
    border: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    color: inherit;
    cursor: pointer;
    font: inherit;
    line-height: inherit;
    overflow: visible;
    vertical-align: inherit;
}
.md-form-item{
    margin:10px;
}
.md-form-item__label {
    text-align: right;
    vertical-align: middle;
    float: left;
    font-size: 14px;
    color: #48576a;
    line-height: 1;
    padding: 11px 12px 11px 0;
    box-sizing: border-box;
}
.md-form-item__content {
    line-height: 36px;
    position: relative;
    font-size: 14px;
}
.md-input__inner {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: #fff;
    background-image: none;
    border-radius: 4px;
    border: 1px solid #333;
    box-sizing: border-box;
    color: #1f2d3d;
    display: block;
    font-size: inherit;
    height: 36px;
    line-height: 1;
    outline: 0;
    padding: 3px 10px;
    transition: border-color .2s cubic-bezier(.645,.045,.355,1);
    width: 100%;
}
.md-input__inner:focus{border-color:#20a0ff}